 затрат и поддержки штанов. Уйдите в отпуск на несколько\n      недель или месяцев: это укрепит нервы и поддержит рабочее\n      состояние грузовиков. Можете перейти на режим, проводя 3 недели за\n      рулём, остальные 3 – отдыхая. Налаживая лайт-график, убедитесь,\n      что можете приостановить страховку. Если в вашей траковой\n      компании нет возможности поставить на “стоп”, подберите новую.")]),_vm._v(" "),_c('p',{staticClass:"bold"},[_vm._v("Рекомендация 6. Переходите под диспетчерское\n      крыло IQHauler: защитим интересы овнера в любых\n      рыночных условиях")]),_vm._v(" "),_c('p',[_vm._v("\n      Громко звучит, но это правда: совершенно новый стиль\n      диспетчеризации с "),_c('a',{staticClass:"iq-link",attrs:{"href":"iqhaulers.com"}},[_vm._v("IQHaulers")]),_vm._v(" в первую очередь ставит во главе\n      стратегии роста финансов овнера. Главное правило – оценка\n      доходности каждой мили и стремление к её непрерывному росту.\n      Уникальность платформы в том, что она помогает диспетчерам\n      учитывать множество нюансов: не просто смотреть на гросс и длину\n      пути, но анализировать тип дороги и рельеф, загрузку и время рейса,\n      погоду и… Много факторов.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      Алгоритм нашего софта включает все те рекомендации, которые\n      изложены выше. Динамичное планирование транспортировки\n      оставьте нашим диспетчерам: именно IQHaulers найдёт правильные\n      грузы, а не будет замыливать этими вопросами голову водителям.\n      Интуиция и практический опыт диспетчеров в дружной связке с\n      глубокой программной аналитикой платформы делают чудеса.\n    ")]),_vm._v(" "),_c('p',{staticClass:"last-paragraph"},[_vm._v("\n      До встречи, овнеры! Желаем хороших гроссов и минимум толлов.\n      Ждём вас нашей дружной командой: мы на связи и готовы всегда\n      ответить на любой вопрос и дать дельный совет, вне зависимости,\n      работаете вы на West или East, Mid-West либо South.\n    ")])])])}]
